/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: linear-gradient(to right, rgba(11, 19, 43, 0.9) 0%, rgba(11, 19, 43, 0.9) 100%), url(../img/20230624_181605.jpg);
  background-size: cover;
  background-position: bottom center;
  background-repeat: no-repeat;
  padding: 3rem;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0 20rem 0;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 3rem 0 27rem 0;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .header__contenido {
    margin-top: 10rem;
    max-width: 50rem;
  }
}
.header__texto {
  color: #ffffff;
  text-align: justify;
  font-family: "Lobster", cursive;
  display: none;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
  }
}
.header__heading {
  color: #ffffff;
  font-family: "Lobster", cursive;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__imagen {
  display: none;
}
@media (min-width: 768px) {
  .header__imagen {
    display: block;
    position: absolute;
    right: 0;
    bottom: -5rem;
    max-width: 35rem;
    height: 30rem;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 5rem;
  }
}
@media (min-width: 1024px) {
  .header__imagen {
    height: 55rem;
  }
}
@media (min-width: 1200px) {
  .header__imagen {
    max-width: 60rem;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }
}
.navegacion__link {
  color: #ffffff;
  font-size: 1.8rem;
  margin-bottom: 1rem;
  transition: color 0.5s;
}
.navegacion__link:last-of-type {
  margin-bottom: 0;
}
.navegacion__link:hover {
  color: #3a506b;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}

.swiper {
  width: 100%;
  height: 80%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  overflow: hidden;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 38rem;
  object-fit: cover;
  position: relative;
}
@media (min-width: 1024px) {
  .swiper-slide img {
    height: 28rem;
  }
}

.menu {
  margin-top: 1rem;
  padding: 6rem 0;
  text-align: center;
  font-family: "Lobster", cursive;
}
@media (min-width: 1024px) {
  .menu {
    margin-top: 8rem;
  }
}
.menu__bebida {
  position: absolute;
  padding: 100% 5rem;
  text-align: justify;
  background-color: #3a506b;
  opacity: 0;
  transition: opacity 0.5s;
}
.menu__bebida:hover {
  opacity: 0.9;
}

.bebida__nombre {
  width: 45%;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
  margin: 0 auto;
  font-weight: 700;
}
.bebida__descripcion {
  color: #ffffff;
}

.paquetes {
  margin-top: 2rem;
  padding: 6rem 0;
  font-family: "Lobster", cursive;
  text-align: center;
  background-color: #3a506b;
  color: #ffffff;
}
.paquetes__heading {
  margin: 0;
}
.paquetes__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .paquetes__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .paquetes__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.paquete {
  background-color: #ffffff;
  padding: 5rem 1rem;
  border-radius: 0.5rem;
}
.paquete__nombre {
  color: #0b132b;
  text-transform: uppercase;
  font-size: 3rem;
}
.paquete__precio {
  color: #3a506b;
  font-size: 2.2rem;
  font-weight: bold;
}
.paquete__descripcion {
  color: #3a506b;
  font-size: 2rem;
}
.paquete__boton {
  background-color: #0b132b;
  padding: 1.5rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color transform;
  transition-duration: 0.3s;
  text-align: center;
}
@media (min-width: 768px) {
  .paquete__boton {
    width: auto;
  }
}
.paquete__boton:hover {
  background-color: #010102;
  transform: scale(1.3);
}
.paquete__svg {
  margin-bottom: 2rem;
}

.contacto {
  margin-top: 5rem;
  display: grid;
  align-items: center;
  font-family: "Lobster", cursive;
}
@media (min-width: 768px) {
  .contacto {
    grid-template-columns: repeat(2, 1fr);
    gap: 15rem;
  }
}
.contacto__informacion {
  text-align: center;
}
.contacto__heading {
  font-weight: bold;
  font-size: 5rem;
  color: #3a506b;
}
.contacto__texto {
  font-size: 2rem;
  color: #3a506b;
}
.contacto__formulario {
  background-color: #3a506b;
  padding: 5rem 2rem;
  border-radius: 0.8rem;
}

.formulario__campo {
  margin-bottom: 1rem;
}
.formulario__campo:last-of-type {
  margin-bottom: 0;
}
.formulario__label {
  color: #fff;
  display: block;
  margin: 1rem 0;
  text-transform: uppercase;
  margin: 2rem 0;
}
.formulario__input {
  border: none;
  padding: 1.5rem;
  width: 100%;
  max-width: 100%;
  border-radius: 0.5rem;
}
.formulario__textarea {
  border: none;
  padding: 1.5rem;
  width: 100%;
  height: 15rem;
  max-width: 100%;
  border-radius: 0.5rem;
  resize: none;
}

.footer {
  margin-top: 3rem;
  padding: 2rem 0;
  background-color: #3a506b;
  text-align: center;
}
.footer__texto {
  font-family: "Lobster", cursive;
  color: #ffffff;
  font-size: 3rem;
  margin: 0;
}